import React from 'react';
import millify from 'millify';
import { Typography,Row,Col, Statistic } from 'antd'
import { Link } from 'react-router-dom';

import { useGetCryptosQuery } from '../Services/cryptoApi';

import {MarketPage,NewsPage,Loader} from '../Components'

import homeIcon from '../Images/homepage-icon.png'
import marketIcon from '../Images/main-logo.png'
import newsIcon from '../Images/news-icon.png'


const {Title} =Typography
const HomePage = () => {
    const { data,isFetching} = useGetCryptosQuery(10);
    const globalStats = data?.data?.stats;
    return (
        <div>
            <Title level={2} className='"heading'>
                <img src={ homeIcon} height={'40px'} alt='' style={{margin:'.5rem'}}/>
                Crypto Currencies Information
            </Title>
                {(isFetching)?(
                    <Loader />
                ):(
                    <>
                        <Row>
                            <Col span={12}><Statistic title="Total CryptoCurrencies" value={globalStats.total}/></Col>
                            <Col span={12}><Statistic title="Total Exchanges" value={millify(globalStats.totalExchanges)}/></Col>
                            <Col span={12}><Statistic title="Total Market Cap" value={millify(globalStats.totalMarketCap)}/></Col>
                            <Col span={12}><Statistic title="Total 24h Volume" value={millify(globalStats.total24hVolume)}/></Col>
                            <Col span={12}><Statistic title="Total Market" value={millify(globalStats.totalMarkets)}/></Col>
                        </Row>                        
                    </>
                )}
                <div className="home-heading-container">
                    
                    <Title level={2} className='home-title'>
                        <img src={ marketIcon} height={'40px'} alt='' style={{margin:'.5rem'}}/>
                        Top Cryptocurrencies
                    </Title>
                    <Title level={4} className='show-more'><Link to="/Market">Show more</Link></Title>
                </div>
                <MarketPage simplified/>
                <div className="home-heading-container">
                    
                    <Title level={2} className='home-title'>
                        <img src={ newsIcon} height={'40px'} alt='' style={{margin:'.5rem'}}/>
                        Top Crypto News
                    </Title>
                    <Title level={4} className='show-more'><Link to="/News">Show more</Link></Title>
                </div>
                <NewsPage simplified/>
        </div>
    )
}

export default HomePage
