import React from 'react'
import {BrowserRouter,Route,Switch,Redirect,Link} from 'react-router-dom'
import {Layout,Typography , Space} from 'antd';

import {Navbar,HomePage,MarketPage,NewsPage,SignupPage,LoginPage,CryptoDetails} from './Components';
import './App.css'

const App = () => {
    return (
        <div className='app' style={{backgroundColor:"rgb(240,242,245)"}}>
            <div className="navbar">
                <Navbar/>
            </div>
            <div className="main" >
                <Layout>
                    <div className="routes">
                            <Switch>
                                <Route exact path="/">
                                    <HomePage/>
                                </Route>
                                <Route path="/Market">
                                    <MarketPage/>
                                </Route>
                                <Route exact path="/crypto/:coinId">
                                    <CryptoDetails />
                                </Route>                                
                                <Route path="/News">
                                    <NewsPage/>
                                </Route>
                                <Route path="/Signup">
                                    <SignupPage/>
                                </Route>
                                <Route path="/Login">
                                    <LoginPage/>
                                </Route>
                            </Switch>
                    </div>
                </Layout>
                
            </div>
        </div>
    )
}

export default App;
